// TimeSpentDaily.vue - Vue Component
<template>
  <div class="time-spent-daily-container">
    <h3>Time Spent on Activities (Daily)</h3>
    <p>in Minutes</p>
    <apexchart type="bar" :options="chartOptions" :series="chartData" height="350" />
    <button @click.prevent="toggleTable" class="view-more-button">
      {{ showTable ? 'View Less' : 'View More' }}
    </button>
    <table v-if="showTable" class="time-spent-table">
      <thead>
        <tr>
          <th>Day</th>
          <th>Time Spent (minutes)</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in data" :key="item.day">
          <td>{{ item.day }}</td>
          <td>{{ item.minutes }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import VueApexCharts from "vue3-apexcharts";
import axios from "axios";

export default {
  name: "TimeSpentDaily",
  components: { apexchart: VueApexCharts },
  data() {
    return {
      data: [],
      showTable: false,
    };
  },
  computed: {
    chartData() {
      return [{ name: "Time Spent", data: this.data.map(item => item.minutes) }];
    },
    chartOptions() {
      return {
        chart: { type: "bar", height: 350 },
        xaxis: { categories: this.data.map(item => item.day) },
        yaxis: { title: { text: "Minutes" } },
      };
    },
  },
  methods: {
    async fetchTimeSpent() {
      try {
        const token = localStorage.getItem("learnerToken");
        const response = await axios.get("http://localhost:8000/timeSpentOnActivities/learner", {
          headers: token ? { Authorization: `Bearer ${token}` } : {},
        });
        this.data = response.data;
      } catch (error) {
        console.error("Error fetching time spent data:", error);
      }
    },
    toggleTable() {
      this.showTable = !this.showTable;
    },
  },
  mounted() {
    this.fetchTimeSpent();
  },
};
</script>

<style scoped>
.time-spent-daily-container {
  padding: 20px;
}
.view-more-button {
  margin-top: 10px;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  background-color: rgb(255, 255, 255);
  color: rgb(189, 186, 186);
  cursor: pointer;
  transition: background-color 0.3s;
}
.view-more-button:hover {
  background-color: rgb(253, 252, 252);
}
.time-spent-table {
  margin-top: 20px;
  width: 100%;
  border-collapse: collapse;
}
.time-spent-table th, .time-spent-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
}
.time-spent-table th {
  background-color: #f5f5f5;
}
</style>
